"use client"

import { useEffect, useState } from "react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Label } from "@/components/ui/label"
import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar"
import { DashboardLayout } from "@/components/dashboard/dashboard-layout"
import { DashboardShell } from "@/components/dashboard-shell"
import { useProfile } from "@/components/hooks/useProfile"
import ProtectedRoute from "@/components/ProtectedRoute"

export default function ProfilePage() {
  const [isEditing, setIsEditing] = useState(false)

  const { profilePic, setProfilePic, userName, setUserName } = useProfile()

  const [preview, setPreview] = useState<string | null>(null)
  const [userData, setUserData] = useState({
    name: "John Doe",
    dob: "1998-06-15",
    gender: "Male",
    email: "john.doe@example.com",
    phone: "+1 234 567 8900",
    university: "New York University",
    address: "123 University Street, NYC",
  })

  useEffect(() => {
    // Load saved preview
    if (profilePic) setPreview(profilePic)
    if (userName) {
      setUserData((prev) => ({ ...prev, name: userName }))
    }
  }, [profilePic, userName])

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      const url = URL.createObjectURL(file)
      setPreview(url)
      setProfilePic(url)
    }
  }

  const handleChange = (field: string, value: string) => {
    setUserData((prev) => ({ ...prev, [field]: value }))
    if (field === "name") setUserName(value)
  }

  return (
    <ProtectedRoute allowedRoles={['student', 'university', 'company', 'admin']}>
    <DashboardLayout >
      <DashboardShell>
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-3xl font-bold">Profile</h2>
          <Button onClick={() => setIsEditing((prev) => !prev)}>
            {isEditing ? "Save" : "Edit Profile"}
          </Button>
        </div>

        {/* Profile Section */}
        <div className="bg-white dark:bg-gray-900 border rounded-lg shadow p-6 space-y-6">
          {/* Basic Info */}
          <div>
            <h3 className="text-xl font-semibold mb-4">Basic Info</h3>
            <div className="flex items-center gap-6">
              <Avatar className="h-8 w-8">
                <AvatarImage
                  src={profilePic || ""}
                  onError={(e) => {
                    e.currentTarget.onerror = null
                    e.currentTarget.src = ""
                  }}
                />
                <AvatarFallback className="bg-blue-100 text-gray-700 font-medium">
                  {userName
                    .split(" ")
                    .map((word) => word[0])
                    .join("")
                    .toUpperCase()}
                </AvatarFallback>
              </Avatar>
              <div className="flex-1">
                <Label className="text-sm block mb-1">Upload Profile Picture</Label>
                <Input type="file" accept="image/*" onChange={handleImageUpload} disabled={!isEditing} />
              </div>
            </div>
            <div className="grid md:grid-cols-2 gap-4 mt-4">
              <div>
                <Label>Name</Label>
                <Input
                  value={userData.name}
                  readOnly={!isEditing}
                  onChange={(e) => handleChange("name", e.target.value)}
                />
              </div>
              <div>
                <Label>Birthday</Label>
                <Input
                  type="date"
                  value={userData.dob}
                  readOnly={!isEditing}
                  onChange={(e) => handleChange("dob", e.target.value)}
                />
              </div>
              <div>
                <Label>Gender</Label>
                <Input
                  value={userData.gender}
                  readOnly={!isEditing}
                  onChange={(e) => handleChange("gender", e.target.value)}
                />
              </div>
            </div>
          </div>

          {/* Contact Info */}
          <div>
            <h3 className="text-xl font-semibold mb-4">Contact Info</h3>
            <div className="grid md:grid-cols-2 gap-4">
              <div>
                <Label>Email</Label>
                <Input
                  value={userData.email}
                  readOnly={!isEditing}
                  onChange={(e) => handleChange("email", e.target.value)}
                />
              </div>
              <div>
                <Label>Phone</Label>
                <Input
                  value={userData.phone}
                  readOnly={!isEditing}
                  onChange={(e) => handleChange("phone", e.target.value)}
                />
              </div>
            </div>
          </div>

          {/* Address Info */}
          <div>
            <h3 className="text-xl font-semibold mb-4">Address</h3>
            <div>
              <Label>University / Organization Address</Label>
              <Input
                value={userData.address}
                readOnly={!isEditing}
                onChange={(e) => handleChange("address", e.target.value)}
              />
            </div>
          </div>
        </div>
      </DashboardShell>
    </DashboardLayout>
    </ProtectedRoute>
  )
}
