"use client"

import { createContext, useContext, useEffect, useState } from "react"
import { useQuery } from "@apollo/client"
import { IS_LOGGED_IN } from '@/app/apollo/operations/auth'

// Enhanced ProfileContextType to include role and authentication status
type ProfileContextType = {
  profilePic: string | null
  userName: string
  userRole: string
  isLoggedIn: boolean
  user: any // The full user object from cache
  isLoading: boolean
  setProfilePic: (url: string) => void
  setUserName: (name: string) => void
}

const ProfileContext = createContext<ProfileContextType | undefined>(undefined)

export function ProfileProvider({ children }: { children: React.ReactNode }) {
  const [profilePic, setProfilePic] = useState<string | null>(null)
  const [userName, setUserName] = useState("")
  
  // Use the same Apollo query that's used in the Sidebar component
  const { data, loading } = useQuery(IS_LOGGED_IN, {
    fetchPolicy: 'cache-and-network', // Get cached data first, then network if needed
  })
  
  const user = data?.currentUser || null
  const isLoggedIn = !!user
  const userRole = user?.role || ""

  // Initialize profile from cache and localStorage
  useEffect(() => {
    // Try to load from localStorage first
    const storedPic = localStorage.getItem("profilePic")
    const storedName = localStorage.getItem("name")
    
    if (storedPic) setProfilePic(storedPic)
    
    // If user is logged in, prioritize cache data
    if (user) {
      // Use user name from cache, fallback to localStorage
      if (user.name) {
        setUserName(user.name)
      } else if (storedName) {
        setUserName(storedName)
      }
      
      // Use avatar from cache if available
      if (user.avatar) {
        setProfilePic(user.avatar)
      }
    } else if (storedName) {
      // If no user in cache, use localStorage
      setUserName(storedName)
    }
  }, [user])

  // Persist profile changes to localStorage
  useEffect(() => {
    if (profilePic) localStorage.setItem("profilePic", profilePic)
    if (userName) localStorage.setItem("name", userName)
  }, [profilePic, userName])

  return (
    <ProfileContext.Provider value={{ 
      profilePic, 
      userName, 
      userRole,
      isLoggedIn,
      user,
      isLoading: loading,
      setProfilePic, 
      setUserName 
    }}>
      {children}
    </ProfileContext.Provider>
  )
}

export function useProfile() {
  const context = useContext(ProfileContext)
  if (!context) throw new Error("useProfile must be used within ProfileProvider")
  return context
}